<template>
  <div id="main" ref="main" @scroll="handleScroll">
    <div
      v-if="loading && !isRunMainAnimate && isDataLoaded"
      class="fixed top-0 left-0 w-full h-full bg-black flex items-center justify-center z-50"
      style="z-index: 1000"
    >
      <div id="lottie"></div>
    </div>
    <!-- 主視覺動畫層 -->
    <div
      v-if="!isRunMainAnimate"
      class="overlay fixed top-0 left-0 w-full h-full bg-black flex items-center justify-center z-50 opacity-100"
      id="sloganOverlay"
    >
      <div
        class="z-10 absolute inset-0 flex items-center justify-center w-full mx-auto"
      >
        <div class="relative master-vision-action">
          <!-- 放置九張圖，方別是 "你"、"可"、"以"、"不"、"同"、"凡"、"響"、"底線圖"、"Everyone could be a strong influencer"-->
          <!-- 根據 mainSloganAnimateConfig 產生-->
          <div
            v-for="(item, index) in mainSloganAnimateConfig"
            :key="index"
            :class="`${item.finalPosition} ${item.width} animate__animated ${item.animation}`"
            class="absolute hidden"
            :id="`sloganAnimate${index}`"
          >
            <img :src="require(`@/assets/${item.src}`)" class="w-full" />
          </div>
          <div class="w-full bottom-0">
            <img
              ref="sloganAnimate10"
              :src="require('@/assets/slogan_animate-10.png')"
              class="w-full"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- 網頁內容 -->
    <div class="fixed right-10 bottom-10 space-y-4 z-40">
      <!-- 若 showButtons 為 ture 顯示 btn_intern_b.png，否則顯示 btn_intern.png，用淡入效果-->
      <a href="internship.html" class="focus:outline-none" target="_blank">
        <img
          v-if="showButtons"
          src="@/assets/ae_fabbtn_intern_black.gif"
          class="intern-icon mx-auto animate__animated animate__backInRight"
        />

        <img
          v-else
          src="@/assets/ae_fabbtn_intern_white.gif"
          class="intern-icon mx-auto animate__animated animate__backInRight"
        />
      </a>
      <button
        v-if="showButtons"
        @click="scrollToMain"
        class="w-full flex justify-center animate__animated animate__backInRight"
      >
        <img src="@/assets/btn_go_top.png" class="go-top-icon mx-auto" />
      </button>
    </div>
    <nav
      :class="{
        'bg-transparent text-white': !navScrolled,
        'bg-white text-black border border-[#D8D8D8]': navScrolled,
      }"
      class="top-nav z-40 fixed top-0 left-0 w-full flex justify-between items-center px-4 md:px-10 py-1 transition-colors duration-300"
    >
      <div class="flex items-center">
        <!-- 手機板 -->
        <div class="sm:hidden flex items-center">
          <!-- 放置漢堡圖  img:icon_hamburger.png-->
          <div class="hamburger" @click="openMobileMenu">
            <img
              :src="
                navScrolled
                  ? require('@/assets/icon_hamburger.png')
                  : require('@/assets/icon_hamburger_menu_white.png')
              "
              class="w-[24px] h-[20px]"
            />
          </div>
        </div>
        <div class="nav-cw-icon nav_icon ml-0">
          <a
            href="https://www.cwlearning.com.tw/"
            class="focus:outline-none"
            target="_blank"
          >
            <img
              :src="require('@/assets/logo-cw.png')"
              alt="天下學習 CW"
              class="h-full"
          /></a>
        </div>
        <div class="nav-cheers-icon nav_icon">
          <a
            href="https://www.cheers.com.tw/"
            class="focus:outline-none"
            target="_blank"
          >
            <img
              :src="require('@/assets/logo-cheers.png')"
              alt="Cheers"
              class="h-full"
          /></a>
        </div>
        <div
          class="nav-line flex items-center mx-[16px]"
          style="opacity: 24%; background-color: #000000"
        ></div>
        <div class="nav-hakuhodo-icon nav_icon">
          <a
            href="https://www.hakuhodo.cn/network/growww-media-co-ltd.html"
            class="focus:outline-none"
            target="_blank"
          >
            <img
              :src="
                navScrolled
                  ? require('@/assets/logo_grow_hakuhodo_b.png')
                  : require('@/assets/logo_grow_hakuhodo_w.png')
              "
              alt="GROW"
              class="h-full"
          /></a>
        </div>
      </div>
      <div class="hidden sm:flex gap-[48px] third-content">
        <button class="focus:outline-none font-noto" @click="scrollToCareer">
          人才招募
        </button>
        <a
          href="internship.html"
          target="_blank"
          class="focus:outline-none font-noto inline-block"
        >
          <button>實習計畫</button>
        </a>
      </div>
    </nav>

    <!-- 背景圖 DIV，該 div 可以被其他 div 覆蓋，放置於最底層，不隨螢幕移動-->
    <div
      id="hero"
      class="top-0 left-0 w-full bg-cover bg-center z-0 relative"
      :style="{
        backgroundImage: `url(${require('@/assets/img_hero.png')})`,
        height: `${heroHeight}px`,
        backgroundPosition: '33% 10%',
        backgroundSize: 'cover',
      }"
    >
      <!-- 放置一個左右上下置中的圖檔 -->
      <div
        class="master-vision z-1 absolute inset-0 flex items-center justify-center mx-auto"
      >
        <!-- 該區塊分為三張圖，分別為左中右，左邊佔10%，且左圖置於最底部，右邊佔5%，置於最上方，中間佔85% -->
        <div class="w-[10%] mt-[25%]">
          <img
            ref="sloganLightningL"
            :src="require('@/assets/img_lightning_l.png')"
            class="sloganLightningL w-full"
          />
        </div>
        <div class="w-[50%] relative">
          <!-- 放置九張圖，方別是 "你"、"可"、"以"、"不"、"同"、"凡"、"響"、"底線圖"、"Everyone could be a strong influencer"-->
          <!-- 根據 mainSloganAnimateConfig 產生-->
          <div
            v-for="(item, index) in mainSloganAnimateConfig"
            :key="index"
            :class="`${item.finalPosition} ${item.width}`"
            class="absolute"
          >
            <img :src="require(`@/assets/${item.src}`)" class="w-full" />
          </div>
          <div class="w-full bottom-0">
            <img
              ref="sloganAnimate10"
              :src="require('@/assets/slogan_animate-10.png')"
              class="w-full"
            />
          </div>
        </div>
        <div class="w-[5%] mb-[28%]">
          <img
            ref="sloganLightningS"
            :src="require('@/assets/img_lightning_s.png')"
            class="sloganLightningS w-full"
          />
        </div>
      </div>
    </div>

    <!-- EVERYONE COULD BE A STRONG INFLUENCER -->
    <div class="everyoneContentCard contentCard pb-5">
      <div class="slantedCardR"></div>
      <!-- 你可以不同凡響 標題 -->
      <div class="text-white mx-auto mt-10 relative z-10">
        <img
          src="@/assets/img_lightning_l.png"
          class="left-[10%] top[20%] sm:left-[23%] sm:top-[120%] absolute z-20 lightL"
          ref="everyoneLightningL"
        />
        <div
          :class="{ 'animate-titleSwingR': isSloganSwinging }"
          class="title-container -rotate-6 p-2 bg-black px-5 sm:px-0 w-4/5 sm:w-auto mx-auto text-center"
          ref="titleSlogan"
        >
          <h1 class="font-bold uppercase tracking-wider section-title mx-auto">
            EVERYONE COULD BE <br class="block sm:hidden" />A STRONG INFLUENCER
          </h1>
        </div>
      </div>

      <!-- 你可以不同凡響 內容 -->
      <div
        class="container mx-auto mt-10 text-center z-10 relative first-content"
      >
        <!-- 使用 Flexbox 水平居中 -->
        <div class="flex items-center justify-center py-4">
          <div class="flex items-center relative">
            <h2 class="font-bold section-title">你可以不同凡響</h2>
            <img
              src="@/assets/img_lightning_s.png"
              class="mx-4 absolute z-10 everyoneLightningS lightS"
              ref="everyoneLightningS"
            />
          </div>
        </div>
        <p>
          在格威博報堂集團，<br
            class="block sm:hidden"
          />每個人都能成為一個強大的影響者
        </p>
        <p>
          在你的主角故事裡，<br
            class="block sm:hidden"
          />無論你來自哪裡，擁有怎樣的背景，<br
            class="block sm:hidden"
          />都有機會將自己的故事轉化為力量
        </p>
        <p>
          這是一個讓個體差異被珍視、<br
            class="block sm:hidden"
          />才華被發揮的舞台，<br
            class="block sm:hidden"
          />每個人都有機會實現自己的獨特影響力
        </p>
        <br />
        <p>
          在屬於你獨一無二的英雄旅途中，<br
            class="block sm:hidden"
          />練就誰也帶不走的視野格局與經驗值
        </p>
        <p>
          我們提供廣告行銷、大型活動、會展、公關、<br
            class="block sm:hidden"
          />數位媒體等「全方位」行銷傳播服務
        </p>
        <p>選一個你喜歡的角色，開啟全新旅途吧</p>
      </div>

      <!-- 品牌介紹 -->
      <div class="container mx-auto mt-10 z-10 relative">
        <div
          v-for="company in companies"
          :key="company.id"
          class="flex flex-row items-center justify-between py-4"
          style="
            border: 1px solid #000000;
            border-top: 0px;
            border-left: 0px;
            border-right: 0px;
          "
        >
          <div class="flex item-left w-full">
            <div class="brand-pic">
              <img
                :src="company.logo"
                :alt="company.name"
                style="
                  cursor: pointer;
                  width: 100%;
                  height: auto;
                  border: 1px solid #d9d9d9;
                "
                @mouseover="isHover = true"
                @mouseleave="isHover = false"
                @click="redirectToCompanyLink(company.id)"
              />
            </div>
            <div
              class="brand-description"
              @click="toggle(company.id)"
              style="text-align: left"
            >
              <p class="font-bold first-content">
                {{ company.name }}
              </p>
              <transition name="expand">
                <p
                  class="mt-1 second-content"
                  v-if="!company.expanded"
                  style="
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;
                  "
                >
                  {{ company.description }}
                </p>
              </transition>
              <transition name="expand">
                <div
                  v-if="company.expanded"
                  class="expand mt-1 second-content"
                  v-html="
                    company.id === 4
                      ? company.content.replace(
                          /\n/g,
                          '<br class=\'hidden sm:block\'/>'
                        )
                      : company.content.replace(/\n/g, '<br>')
                  "
                ></div>
              </transition>
            </div>
            <button
              @click="toggle(company.id)"
              class="brand-button text-gray-700 mt-2 sm:mt-0 relative"
            >
              <!-- 若是expanded 顯示 btn_collapse.png 若否 btn_expand.png-->
              <img
                v-if="company.expanded"
                src="@/assets/btn_collapse.png"
                alt="Collapse"
                style="width: 80%; height: auto"
              />
              <img
                v-else
                src="@/assets/btn_expand.png"
                alt="Expand"
                style="width: 80%; height: auto"
              />
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- CREATIVITY -->
    <div class="contentCard pb-5 z-10 pt-10 sm:pt-40">
      <!-- CREATIVITY  標題 -->
      <div class="text-white mx-auto mt-10 relative">
        <img
          src="@/assets/img_lightning_l.png"
          class="left-[20%] top-[65%] sm:left-[35%] sm:top-[65%] absolute lightL z-10"
          ref="creativityLightningL"
        />
        <div
          :class="{ 'animate-titleSwingR': isCreativitySwinging }"
          class="title-container -rotate-6 p-2 bg-black w-4/5 sm:w-3/5 mx-auto text-center"
          ref="titleCreativity"
        >
          <h1
            class="font-bold uppercase tracking-wider text-center section-title mx-auto"
          >
            CREATIVITY
          </h1>
        </div>
      </div>

      <!-- CREATIVITY 內容 -->
      <div
        class="container mx-auto mt-10 text-center z-10 relative first-content"
      >
        <!-- 使用 Flexbox 水平居中 -->
        <div class="flex items-center justify-center py-4">
          <div class="flex items-center relative">
            <h2 class="font-bold section-title">
              我們不僅只是工作者，<br
                class="block sm:hidden"
              />更是改變者、創造者
            </h2>
            <img
              src="@/assets/img_lightning_s.png"
              class="mx-4 absolute z-10 mr-5 sm:mr-0 creativityLightningS lightS"
              ref="creativityLightningS"
            />
          </div>
        </div>
        <p>
          廣告、數據、會展與公關<br
            class="block sm:hidden"
          />正形塑著大眾所看到的世界
        </p>
        <p>
          我們貼近生活者的生活樣貌，<br
            class="block sm:hidden"
          />用團隊的能力與思維創造價值
        </p>
        <p>
          同時為生活帶來更多的積極影響，<br
            class="block sm:hidden"
          />這裡就是你的舞台
        </p>
      </div>
    </div>

    <!-- Creativity 圖片 PC版 一列 3 欄一列，width 100%，手機板則為 2 欄一列 -->
    <div class="mx-auto mt-10 w-full z-10 relative">
      <div class="grid grid-cols-2 sm:grid-cols-3">
        <div
          v-for="index in isMobile ? 10 : 9"
          :key="index"
          class="col-span-1 overflow-hidden hidden sm:block"
        >
          <a :href="works[index - 1].url" target="_blank">
            <img
              :src="require(`@/assets/${works[index - 1].img}`)"
              alt="Creativity"
              class="w-full hover:scale-110 transition-transform duration-300"
            />
          </a>
        </div>
      </div>
    </div>
    <div id="career" ref="career"></div>

    <!-- 徵才資訊 -->
    <!-- CAREER -->
    <div class="contentCard pb-0 sm:pb-5 pt-10 sm:pt-40">
      <div class="slantedCardL"></div>
      <!-- CAREER 標題 -->
      <div class="text-white mx-auto mt-10 relative z-20">
        <img
          src="@/assets/img_lightning_l.png"
          class="right-[20%] top-[60%] sm:right-[35%] sm:top-[65%] absolute lightL z-10"
          ref="careerLightningR"
        />
        <div
          :class="{ 'animate-titleSwingL': isCareerSwinging }"
          class="title-container rotate-6 p-2 bg-black w-4/5 sm:w-3/5 mx-auto text-center"
          ref="titleCareer"
        >
          <h1
            class="font-bold uppercase tracking-wider text-center section-title mx-auto"
          >
            CAREER
          </h1>
        </div>
      </div>

      <!-- CAREER 內容 -->
      <div
        class="container mx-auto mt-10 text-center z-10 relative first-content"
      >
        <!-- 使用 Flexbox 水平居中 -->
        <div class="flex items-center justify-center py-4">
          <div class="flex items-center relative">
            <img
              src="@/assets/img_lightning_s.png"
              class="mx-4 absolute z-10 careerLightningS lightS"
              ref="careerLightningS"
            />
            <h2 class="font-bold section-title">
              在格威博報堂職涯不只是一個軌道
              <br class="block sm:hidden" />而是一片充滿機會的曠野
            </h2>
          </div>
        </div>
        <p>
          身為國內最具影響力的全方位行銷傳播集團，<br
            class="block sm:hidden"
          />我們服務著來自不同領域的全球頂尖客戶
        </p>
        <p>
          格威博報堂集團提供多元的工作機會，<br
            class="block sm:hidden"
          />可以依據您的所長以及興趣，<br
            class="block sm:hidden"
          />找到最適合自己的職位<br class="block sm:hidden" />
        </p>
        <p>
          這是一個讓個體差異被珍視、才華被發揮的舞台，每個人都有機會實現自己的獨特影響力
        </p>
        <br />
        <p>
          集團人才將有機會接觸到不同產業，<br
            class="block sm:hidden"
          />站在業界脈動的第一線更能了解各產業趨勢<br class="block sm:hidden" />
        </p>
        <p>
          並且將自己的創意、能力付諸實踐，<br
            class="block sm:hidden"
          />當跨域成為習慣，你將有著無比精彩的可能性
        </p>
      </div>

      <!-- 徵才資訊 -->
      <div class="container mx-auto mt-10 z-10 relative">
        <!-- 兩個並排的下拉選單，彼此保留一點間距，展開的下拉選單底色為#F2F0EF，展開的浮動視窗分兩欄，左欄為5%的 打勾樣式表示為當前所選，剩下為下拉選項 -->
        <div class="flex justify-start gap-2 sm:gap-4">
          <div class="w-auto">
            <select
              v-model="selectedCompany"
              class="w-auto bg-[#F2F0EF] py-1 pl-1 sm:px-2 focus:outline-none second-content"
            >
              <option
                v-for="option in companyOptions"
                :key="option"
                :value="option"
                class="second-content"
              >
                {{ option }}
              </option>
            </select>
          </div>
          <div class="w-auto">
            <select
              v-model="selectedCategory"
              class="w-auto bg-[#F2F0EF] py-1 pl-1 sm:px-2 focus:outline-none second-content"
            >
              <option
                v-for="option in categoryOptions"
                :key="option"
                :value="option"
                class="second-content"
              >
                {{ option }}
              </option>
            </select>
          </div>
        </div>

        <!-- 根據 articles 內容，每筆分為三欄，每筆底下有一條黑色分隔線，左欄由上至下顯示標題(字較大)+內容，總佔寬50%，中間欄由上至下顯示學歷,經歷,地點，總佔寬35%，右欄由上至下顯示一個黑色底按鈕 "立即應徵"，點擊後連至 link，總佔寬15%，按鈕靠右，手機板則將三欄排成直的 -->
        <div
          class="job-container mt-3 max-h-[500px] overflow-y-scroll customScrollBar"
        >
          <div
            v-for="job in jobFilterResults"
            :key="job.id"
            class="flex flex-col sm:flex-row items-start justify-between border-b-1 py-4 px-4"
          >
            <div class="w-full sm:w-1/2 pr-0 sm:pr-10 mb-4 sm:mb-0">
              <p class="font-bold first-content">
                {{ job.title }} {{ job.position }}
              </p>
              <p
                class="text-black mt-1 second-content"
                v-html="job.description.replace(/\n/g, '<br />')"
              ></p>
            </div>
            <div class="w-full sm:w-1/3 pl-0 sm:pl-20 mb-4 sm:mb-0">
              <p class="text-black mt-1 second-content">
                學歷：{{ job.education }}
              </p>
              <p class="text-black mt-1 second-content">
                經歷：{{ job.experience }}
              </p>
              <p class="text-black mt-1 second-content">
                地點：{{ job.location }}
              </p>
            </div>
            <div
              class="w-full sm:w-1/6 flex items-center justify-start sm:justify-end mb-4 sm:mb-0"
            >
              <button
                class="text-white bg-black px-4 py-2 focus:outline-none third-content w-full sm:w-auto"
                @click="redirectToJobLink(job.url)"
              >
                立即應徵
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 跑馬燈 -->
    <div class="marquee mt-10 sm:mt-40">
      <div class="logoMarquee track gap-2" ref="logoMarquee">
        <!-- 動態計算剛好可填滿螢幕需要幾個 span "Everyone could be a strong influencer"-->
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
        <span class="text">Everyone could be a strong influencer</span>
      </div>
    </div>

    <!-- HEROES -->
    <div class="contentCard pb-5 z-10 bg-white mt-10 sm:mt-40">
      <!-- HEROES  標題 -->
      <div class="text-white mx-auto mt-10 relative">
        <img
          src="@/assets/img_lightning_l.png"
          class="left-[25%] top-[65%] sm:left-[35%] sm:top-[65%] absolute lightL z-10"
          ref="heroesLightningL"
        />
        <div
          :class="{ 'animate-titleSwingR': isHeroesSwinging }"
          class="title-container -rotate-6 p-2 bg-black w-4/5 sm:w-3/5 mx-auto text-center"
          ref="titleHeroes"
        >
          <h1
            class="font-bold uppercase tracking-wider text-center section-title mx-auto"
