// 徵才篩選條件
const companyOptions = ref([]);
const categoryOptions = ref([]);

const isDataLoaded = ref(false);
// 徵才篩選 selectedCompany 預設會是 companyOptions 的第一個選項， categoryOptions 亦然
const selectedCompany = ref();
const selectedCategory = ref();
// 徵才篩選
const jobFilterResults = ref([...jobs.value]);
// HEROES
const heroes = ref(dataHeroes);
// 手機板只需要 id 1、6、13、17、20、21
const heroesMobile = ref([
  dataHeroes[0],
  dataHeroes[5],
  dataHeroes[12],
  dataHeroes[16],
  dataHeroes[19],
  dataHeroes[20],
]);
const isMobile = ref(false);
const modalOpen = ref(false);
const mobileMenuOpen = ref(false);
const currentHero = ref({});
// HEROES 跳窗關閉動畫
const closeAnimation = ref(false);
// Menu 跳窗關閉動畫
const closeMenuAnimation = ref(false);
// 右側按鈕滑動監控
const showButtons = ref(false);
// 5張LOGO
const logoPic = ref([
  {
    img: "logo_ucg.png",
    url: "https://www.ucgroup.com.tw",
  },
  {
    img: "logo_interplan.png",
    url: "https://www.interplan.group",
  },
  {
    img: "logo_mdcg.png",
    url: "https://www.medialand.tw",
  },
  {
    img: "logo_ky_post_design.png",
    url: "https://www.ky-post.com",
  },
  {
    img: "logo_pilot_hakuhodo.png",
    url: "https://pilothakuhodo.com",
  },
]);
// 作品集
const works = ref(dataWorks);

// 主視覺動畫層
const mainSloganAnimateConfig = ref(sloganAnimateConfig);

// loading
const isRunMainAnimate = inject("isRunMainAnimateFlag");
const loading = ref(true);

const career = ref(null);

// ---- 函式 -----------------------------------------

// 連結至公司網站
const redirectToCompanyLink = (id) => {
  const company = companies.value.find((c) => c.id === id);
  if (company) {
    window.open(company.link, "_blank");
  }
};

// 連結至徵才網站
const redirectToJobLink = (url) => {
  window.open(url, "_blank");
};

// 滑動監控
const handleScroll = () => {
  // 獲取 main 的 scrollY
  scrollValue.value = main.value.scrollTop;
  navScrolled.value = scrollValue.value > 300;
  showButtons.value = scrollValue.value > 300;
  titleSloganAction();
  titleCreativityAction();
  titleCareerAction();
  titleHeroesAction();
  titleWorkplaceAction();
  titleGroupAction();
  titleTalentAction();
};

// 根據篩選條件篩選徵才資訊，若選擇 公司類別(不拘) 與 職務類別(不拘) 則顯示全部
const filterJobs = () => {
  if (
    selectedCompany.value === "公司類別(不拘)" &&
    selectedCategory.value === "職務類別(不拘)"
  ) {
    jobFilterResults.value = jobs.value;
  } else if (selectedCompany.value === "公司類別(不拘)") {
    jobFilterResults.value = jobs.value.filter(
      (job) => job.category === selectedCategory.value
    );
  } else if (selectedCategory.value === "職務類別(不拘)") {
    jobFilterResults.value = jobs.value.filter(
      (job) => job.company === selectedCompany.value
    );
  } else {
    jobFilterResults.value = jobs.value.filter(
      (job) =>
        job.company === selectedCompany.value &&
        job.category === selectedCategory.value
    );
  }
};

// HEROES 跳窗
function openModal(index) {
  currentHero.value = heroes.value[index - 1];
  modalOpen.value = true;
  closeAnimation.value = false;
}

function closeModal() {
  closeAnimation.value = true;
  setTimeout(() => {
    modalOpen.value = false;
    closeAnimation.value = false;
  }, 1000);
}

// Menu 跳窗
function openMobileMenu() {
  mobileMenuOpen.value = true;
  closeMenuAnimation.value = false;
}

function closeMobileMenu() {
  closeMenuAnimation.value = true;
  setTimeout(() => {
    mobileMenuOpen.value = false;
    closeMenuAnimation.value = false;
  }, 1000);
}

const initializePage = () => {
  loading.value = true;
  // window.addEventListener("scroll", handleScroll);
  lottie.loadAnimation({
    container: document.getElementById("lottie"), // 动画的挂载点
    renderer: "svg",
    loop: true,
    autoplay: true,
    path: "./loading.json", // 替换为你的 Lottie 文件路径
  });
  sloganAction();
  everyoneAction();
  creativityAction();
  careerAction();
  logoMarqueeAction();
  heroesAction();
  workplaceAction();
  groupAction();
  talentAction();
  window.addEventListener("load", () => {
    // 至少等待 1 秒
    setTimeout(() => {
      loading.value = false;
      startAnimation();
    }, 1000);
  });
  handleResize();
};

const handleResize = () => {
  // 獲取當前螢幕寬度，若大於 768 則將變數 isMobile 改為 true
  if (window.innerWidth > 768) {
    isMobile.value = false;
  } else {
    isMobile.value = true;
  }
};

const loadData = async () => {
  try {
    const response = await fetch("data.json");
    const dataJson = await response.json();
    jobs.value = dataJson.jobs;
    companyOptions.value = dataJson.companyOptions;
    categoryOptions.value = dataJson.categoryOptions;
    isDataLoaded.value = true;
  } catch (error) {
    console.error("Error loading the data:", error);
  }
};

onMounted(() => {
  loadData();
  initializePage();
  window.addEventListener("resize", handleResize);
});

onUnmounted(() => {
  window.removeEventListener("load", startAnimation);
  ScrollTrigger.getAll().forEach((st) => st.kill());
  // 移除 resize 偵測
  window.removeEventListener("resize", handleResize);
});

watch([selectedCompany, selectedCategory], () => {
  filterJobs();
});

watch(
  jobs,
  (newJobs) => {
    jobFilterResults.value = [...newJobs];
  },
  {
    immediate: true,
  }
);

watch(
  companyOptions,
  (newCompanyOptions) => {
    if (newCompanyOptions.length > 0) {
      selectedCompany.value = newCompanyOptions[0];
    }
  },
  {
    immediate: true,
  }
);

watch(
  categoryOptions,
  (newCategoryOptions) => {
    if (newCategoryOptions.length > 0) {
      selectedCategory.value = newCategoryOptions[0];
    }
  },
  {
    immediate: true,
  }
);

// 滑動
const scrollToMain = () => {
  if (main.value) {
    const topPos = 0;
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

const scrollToCareer = () => {
  if (main.value) {
    const topPos = career.value.getBoundingClientRect().top;
    if (isMobile.value) {
      closeMenuAnimation.value = true;
      setTimeout(() => {
        mobileMenuOpen.value = false;
        closeMenuAnimation.value = false;
        main.value.scrollTo({
          top: topPos,
          behavior: "smooth",
        });
      }, 800);
    }
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

