// ==== 品牌介紹展開 =====================================
const toggle = (id) => {
  const company = companies.value.find((c) => c.id === id);
  // 切換展開狀態，其他公司收起
  if (company) {
    company.expanded = !company.expanded;
    companies.value.forEach((c) => {
      if (c.id !== id) {
        c.expanded = false;
      }
    });
  }
};

