const logoMarqueeAction = () => {
  // 用 GASP 做出無縫的跑馬燈
  const animationDistance = logoMarquee.value.offsetWidth / 2;
  gsap.to(".logoMarquee", {
    x: -animationDistance,
    duration: 60,
    ease: "none",
    repeat: -1,
  });
};

